export const replaceWithPH = (stringWithPlaceholders, replacements) =>
  stringWithPlaceholders.replace(/{\w+}/g, (placeholderWithDelimiters) => {
    const placeholderWithoutDelimiters = placeholderWithDelimiters.substring(
      1,
      placeholderWithDelimiters.length - 1
    );
    const stringReplacement =
      replacements[placeholderWithoutDelimiters] || placeholderWithDelimiters;
    return stringReplacement;
  });

export const format = (fmt, ...args) => {
  return fmt
    .split("%%")
    .reduce((aggregate, chunk, i) => aggregate + chunk + (args[i] || ""), "");
};
