import * as React from "react";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import { Typeahead } from "react-bootstrap-typeahead";
import { WrappedParse as Parse } from "../vendor/ParseApp";
import { getPdfText } from "../helpers/pdf";
import { countries } from "../helpers/country";
import { employmentType, occupation } from "../helpers/occupation";
import AppContext from "../context/AppContext";
import Accordion from "react-bootstrap/Accordion";
import ListGroup from "react-bootstrap/ListGroup";
import { flatten } from "../helpers/object";
import { replaceWithPH } from "../helpers/string";
import Alert from "react-bootstrap/Alert";
import {
  Trans,
  useTranslation,
  I18nextContext,
} from "gatsby-plugin-react-i18next";
import { format } from "../helpers/string";

export default function App() {


  console.log(occupation.filter(e => !e.label))

  const { useEffect, useContext, useState } = React;
  const { t } = useTranslation();

  const [hasProfile, setHasProfile] = useState(false);
  const [hasApplication, setHasApplication] = useState(false);
  const [requests, setRequest] = useState([]);
  const options = ["testt"];
  const [file, setFile] = React.useState(null);
  const [model, setModel] = React.useState({
    tags: [],
    location: [{ id: "SG", label: "Singapore" }],
  });

  const setModelValue = (name, value) => {
    setModel({
      ...model,
      [name]: value,
    });
  };

  const context = useContext(AppContext);

  useEffect(() => {
    Parse.Cloud.run("getMyProfile").then((profile) => {
      setHasProfile(!!profile);
    });
    Parse.Cloud.run("getMyRequest").then((items) => setRequest(items));
  }, []);

  useEffect(() => {
    context.showLoading(true);
    Parse.Cloud.run("getMyApplication").then((app) => {
      setHasApplication(!!app);
      if (app) {
        setModel(app.get("data"));
      }
      context.showLoading(false);
    });
  }, [context.showLoading]);

  return (
    <>
      <Container className="">
        <Row>
          <Col>
            {(!hasProfile || !hasApplication || !model.consent) && (
              <Alert key={"primary"} variant={"primary"}>
                {t("profileNotSearchable")}
                <ol>
                  {!hasProfile && <li> {t("noprofile")}</li>}
                  {!hasApplication && <li> {t("noapplication")}</li>}
                  {!model.consent && (
                    <li> {format(t("noconsent"), t("openToWork"))} </li>
                  )}
                </ol>
              </Alert>
            )}
          </Col>
        </Row>
        <Row>
          <Col>
            <Form.Group>
              <Form.Label>Seeking Job</Form.Label>
              <Typeahead
                allowNew={true}
                id="basic-typeahead-multiple"
              
                multiple
                onChange={(d) => setModelValue("tags", d)}
                options={occupation}
                placeholder="Type in or select from  the list of jobs you are looking for"
                selected={model.tags}
              />
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Group>
                <Form.Label>Location to work in</Form.Label>
                <Typeahead
                  allowNew={true}
                  id="basic-typeahead-multiple"
                  labelKey="label"
                  multiple
                  selected={model.location}
                  onChange={(d) => setModelValue("location", d)}
                  options={countries}
                  placeholder="Type in or select from  the list"
                />
              </Form.Group>
            </Form.Group>
          </Col>
          <Col>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Citizenship</Form.Label>
              <Form.Control
                type="text"
                placeholder=""
                value={model.citizenship}
                onChange={(d) => setModelValue("citizenship", d.target.value)}
              />
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Label>
                Resume CV (Please avoid personal data in your cv)
              </Form.Label>
              <Form.Control
                name="document"
                type="file"
                rows={3}
                onChange={async (e) => {
                  setFile(e.target.files[0]);
                }}
              />

              {model.docUrl ? (
                <a className="color-brand-1" href={model.docUrl}>
                  {" "}
                  Download File{" "}
                </a>
              ) : (
                ""
              )}
            </Form.Group>
          </Col>

          <Col>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Group>
                <Form.Label>Employment Type</Form.Label>
                <Typeahead
                  allowNew={true}
                  id="basic-typeahead-multiple"
                  labelKey="label"
                  multiple
                  selected={model.employmentType}
                  onChange={(d) => setModelValue("employmentType", d)}
                  options={employmentType}
                  placeholder="Type in or select from  the list"
                />
              </Form.Group>
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Expected Salary</Form.Label>
              <Form.Control
                type="text"
                placeholder=""
                value={model.expectSalary}
                onChange={(e) => setModelValue("expectSalary", e.target.value)}
              />
            </Form.Group>
          </Col>

          <Col>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Notice Period</Form.Label>
              <Form.Control
                type="text"
                placeholder=""
                value={model.noticePeriod}
                onChange={(e) => setModelValue("noticePeriod", e.target.value)}
              />
            </Form.Group>
          </Col>
        </Row>
        <Row>
          <Col>
            <Form>
              <Form>
                <Form.Check
                  type="switch"
                  id="custom-switch"
                  label={t("openToWork")}
                  checked={model.consent}
                  onChange={() => {
                    setModelValue("consent", !model.consent);
                  }}
                />
              </Form>

              <Button
                variant="primary"
                onClick={async () => {
                  context.showLoading(true);
                  if (file) {
                    const parseFile = new Parse.File("document", file);
                    const document = await parseFile.save();
                    const data = await document.getData();
                    const output = await getPdfText(atob(data));
                    const url = document.url();

                    const newModel = {
                      ...model,
                      docUrl: url,
                    };
                    setModel(newModel);
                    await Parse.Cloud.run("updateApplication", {
                      search_field: output,
                      model: {
                        data: newModel,
                      },
                    });
                  } else {
                    await Parse.Cloud.run("updateApplication", {
                      model: {
                        data: model,
                      },
                    });
                  }

                  context.showLoading(false);
                }}
              >
                Update
              </Button>
            </Form>
          </Col>
        </Row>
        <Row>
          <Col>
            <br />
            <Form>
              {model?.consent && (
                <Accordion defaultActiveKey={["0"]} alwaysOpen>
                  <Accordion.Item eventKey={0}>
                    <Accordion.Header>
                      {requests.length
                        ? format(t("numberConnectionWaiting"), requests.length)
                        : t("waitingForConnection")}
                    </Accordion.Header>
                    <Accordion.Body>
                      {!requests.length && (
                        <ol>
                          <li>{t("profileDataClause")}</li>
                          <li>{t("appDataClause")}</li>
                        </ol>
                      )}
                      <ListGroup>

                        {
                          !requests.length && <div>People who request for your profile will appear here</div>
                        }
                        {requests.map((req, i) => {
                          const flatObject = flatten(req.get("data"));
                          return (
                            <ListGroup.Item>
                              {replaceWithPH(
                                "{name} ({email}) wish to connect with you",
                                flatObject
                              )}
                              <Button
                                onClick={() => {
                                  Parse.Cloud.run("acceptRequest", {
                                    id: req.id,
                                  });
                                }}
                              >
                                Accept
                              </Button>
                            </ListGroup.Item>
                          );
                        })}
                      </ListGroup>
                    </Accordion.Body>
                  </Accordion.Item>
                </Accordion>
              )}
            </Form>
          </Col>
        </Row>
      </Container>
    </>
  );
}

export const Head: HeadFC = () => (
  <>
    <title></title>
    <script src="/assets/pdf/pdf.js" type="text/javascript" />
  </>
);
